import { create } from "zustand"
import { persist } from "zustand/middleware"
import type { AIVoiceSettings, AIVoiceProfile } from "./ai-voice-types"

export type VoiceType = "standard-male" | "standard-female" | "azan-islamic" | "hausa" | "twi" | "arabic"
export type Language = "english" | "hausa" | "twi" | "arabic"

export interface Student {
  id: string
  firstName: string
  lastName: string
  class?: string
}

export interface Timetable {
  id: string
  name: string
  day: string
  bellTime: string
  bellType: string
}

export interface Device {
  id: string
  name: string
  type: "esp32" | "raspberry-pi"
  status: "online" | "offline"
  lastSeen: string
}

export interface CallRequest {
  id: string
  studentId: string
  message: string
  timestamp: string
  status: "pending" | "played" | "failed"
  voice: VoiceType
  language: Language
}

export interface SystemSettings {
  defaultVoice: VoiceType
  defaultLanguage: Language
  azanEnabled: boolean
  prayerTimes?: {
    fajr: string
    dhuhr: string
    asr: string
    maghrib: string
    isha: string
  }
  // AI Voice settings - optional for backward compatibility
  aiVoice?: AIVoiceSettings
}

interface SchoolStore {
  students: Student[]
  timetables: Timetable[]
  devices: Device[]
  callRequests: CallRequest[]
  settings: SystemSettings
  addStudent: (student: Student) => void
  removeStudent: (id: string) => void
  addTimetable: (timetable: Timetable) => void
  removeTimetable: (id: string) => void
  updateTimetable: (id: string, timetable: Partial<Timetable>) => void
  addDevice: (device: Device) => void
  removeDevice: (id: string) => void
  addCallRequest: (request: CallRequest) => void
  clearCallRequests: () => void
  updateSettings: (settings: Partial<SystemSettings>) => void
}

export const useStore = create<SchoolStore>()(
  persist(
    (set) => ({
      students: [
        { id: "1", firstName: "Ama", lastName: "Serwaa", class: "JHS 2 Blue" },
        { id: "2", firstName: "Kwame", lastName: "Mensah", class: "JHS 3 Red" },
        { id: "3", firstName: "Mohammed", lastName: "Ibrahim", class: "JHS 1 Green" },
      ],
      timetables: [
        { id: "1", name: "Morning Assembly", day: "Monday", bellTime: "08:00", bellType: "announcement" },
        { id: "2", name: "Break Time", day: "Daily", bellTime: "10:30", bellType: "bell" },
      ],
      devices: [
        { id: "1", name: "Office Speaker", type: "esp32", status: "online", lastSeen: new Date().toISOString() },
        { id: "2", name: "Classroom PI", type: "raspberry-pi", status: "online", lastSeen: new Date().toISOString() },
      ],
      callRequests: [],
      settings: {
        defaultVoice: "standard-male",
        defaultLanguage: "english",
        azanEnabled: false,
        prayerTimes: {
          fajr: "05:30",
          dhuhr: "12:30",
          asr: "15:00",
          maghrib: "17:45",
          isha: "19:00",
        },
        // Default AI voice settings
        aiVoice: {
          aiVoiceEnabled: false,
          primaryProvider: "openai",
          fallbackProvider: "azure",
          voiceProfiles: {
            announcement: {
              id: "default-announcement",
              name: "Professional Announcer",
              language: "english",
              gender: "neutral",
              category: "announcement",
              provider: "openai"
            },
            prayer: {
              id: "default-prayer",
              name: "Islamic Prayer Voice",
              language: "arabic",
              gender: "male",
              category: "religious",
              provider: "openai"
            },
            bell: {
              id: "default-bell",
              name: "School Bell Voice",
              language: "english",
              gender: "neutral",
              category: "bell",
              provider: "openai"
            },
            general: {
              id: "default-general",
              name: "General Purpose Voice",
              language: "english",
              gender: "female",
              category: "standard",
              provider: "openai"
            }
          },
          cacheSettings: {
            maxSize: 100, // 100MB
            maxAge: 30, // 30 days
            enabled: true
          },
          usageSettings: {
            monthlyLimit: 100000, // 100k characters
            costThreshold: 50, // $50 USD
            optimizationEnabled: true
          },
          providerConfigs: {
            openai: {
              apiKey: "",
              enabled: false,
              priority: 1,
              rateLimit: {
                requestsPerMinute: 50,
                charactersPerDay: 50000
              }
            },
            elevenlabs: {
              apiKey: "",
              enabled: false,
              priority: 2,
              rateLimit: {
                requestsPerMinute: 20,
                charactersPerDay: 20000
              }
            },
            azure: {
              apiKey: "",
              endpoint: "",
              enabled: false,
              priority: 3,
              rateLimit: {
                requestsPerMinute: 100,
                charactersPerDay: 100000
              }
            }
          }
        }
      },
      addStudent: (student) => set((state) => ({ students: [...state.students, student] })),
      removeStudent: (id) => set((state) => ({ students: state.students.filter((s) => s.id !== id) })),
      addTimetable: (timetable) => set((state) => ({ timetables: [...state.timetables, timetable] })),
      removeTimetable: (id) => set((state) => ({ timetables: state.timetables.filter((t) => t.id !== id) })),
      updateTimetable: (id, updates) =>
        set((state) => ({
          timetables: state.timetables.map((t) => (t.id === id ? { ...t, ...updates } : t)),
        })),
      addDevice: (device) => set((state) => ({ devices: [...state.devices, device] })),
      removeDevice: (id) => set((state) => ({ devices: state.devices.filter((d) => d.id !== id) })),
      addCallRequest: (request) => set((state) => ({ callRequests: [...state.callRequests, request] })),
      clearCallRequests: () => set(() => ({ callRequests: [] })),
      updateSettings: (newSettings) => set((state) => ({ settings: { ...state.settings, ...newSettings } })),
    }),
    {
      name: "school-bell-storage",
    },
  ),
)
